@font-face {
  font-family: 'GraphikLCG-light';

  src: local('Graphik LCG Light'), local('GraphikLCG-Light'),
    url('./fonts/GraphikLCG-Light.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'GraphikLCG-semibold';

  src: local('Graphik LCG Semibold'), local('GraphikLCG-Semibold'),
    url('./fonts/GraphikLCG-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}

:root {
  --color-black: rgba(25, 28, 31, 1);
  --color-dark-gray: rgba(94, 100, 106, 1);
  --color-gray: rgba(131, 137, 143, 1);
  --color-mid-gray: rgba(194, 200, 205, 1);
  --color-light-gray: rgba(236, 239, 242, 1);
  --color-extralight-gray: rgba(246, 248, 250, 1);
  --color-white: rgba(255, 255, 255, 1);
  --color-dangerous: rgba(241, 54, 54, 1);
  --color-green-success: rgba(37, 205, 130, 1);
  --color-primary-disabled: rgba(171, 202, 230, 1);
  --color-primary-hover: rgba(57, 125, 255, 1);
  --color-primary: rgba(42, 108, 234, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;

  font-family: 'GraphikLCG-semibold';
  line-height: 2.4rem;
  color: var(--color-white);

  background-color: rgba(255, 255, 255, 1);
  overflow-x: hidden;

  scroll-behavior: smooth;
}

.section {
  margin: 0 auto;
  max-width: 122rem;
  color: var(--color-black);
}

.container {
  width: 100vw;
  background-color: var(--color-extralight-gray);
  padding: 3rem;
  margin-top: 8rem;
}
/* HEADER */

.header {
  position: relative;
  margin-bottom: 8rem;
  width: 100vw;
  height: 100vh;
  background-image: url(./img/Lunch_atop_a_Skyscraper\ 1.jpg);
}

.header__overlay {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: var(--color-primary);
  opacity: 80%;
}

/* NAVIGATION */

.nav {
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 122rem;
  padding: 2.4rem;
  z-index: 100;
}

.nav__logo {
  width: 12rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
}

.circle {
  width: 2.4rem;
  height: 2.4rem;
  border-radius: 50%;
}

.blueCircle {
  position: relative;
  z-index: 5;
  background-color: var(--color-primary);
}

.whiteCircle {
  position: relative;
  z-index: 4;
  left: -1.2rem;
  background-color: var(--color-white);
}

.logo__text {
  font-size: 2rem;
}

.nav__links {
  display: flex;
  align-items: center;
  list-style: none;
}

.nav__item {
  margin-left: 2.4rem;
}

.nav__link {
  font-family: 'GraphikLCG-light';
  font-size: 1.6rem;
  color: var(--color-white);
  text-decoration: none;
}

.nav__link:hover {
  text-decoration: underline;
}

/* GREETING */

.header__greeting {
  position: absolute;
  z-index: 50;
  max-width: 74.8rem;
  left: calc(50vw - 37.4rem);
  top: calc(50vh - 16.7rem);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.greeting__header {
  text-align: center;
  font-size: 5.4rem;
  line-height: 6.4rem;
  margin-bottom: 1.8rem;
}

.greeting__text {
  text-align: center;
  font-family: 'GraphikLCG-light';
  font-size: 1.8rem;
  line-height: 3.2rem;
  margin-bottom: 9.2rem;
}

.greeting__btn {
  padding: 2rem 4rem;
  border-radius: 0.5rem;
  border-width: 0;
  background-color: var(--color-light-gray);
  font-family: 'GraphikLCG-semibold';
  color: var(--color-black);
  font-size: 2rem;

  cursor: pointer;
  transition: ease-in-out 0.2s;
}

.greeting__btn:hover {
  background-color: var(--color-white);
}
/* SECTION_1 */

.firstSection {
  margin-bottom: 4.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6.1rem;
}

.firstSection__title {
  font-size: 4rem;
}

.firstSection__cards {
  display: flex;
  flex-wrap: wrap;
  column-gap: 3.2rem;
}

.card {
  width: 28rem;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
  margin-bottom: 3.2rem;
}

.card__img {
  width: 7.2rem;
  height: 7.2rem;
}

.card__desc {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.card__title {
  font-size: 1.6rem;
}

.card__text {
  font-family: 'GraphikLCG-light';
  font-size: 1.4rem;
}

/* SECTION_2 */

.secondSection {
  display: flex;
  align-items: center;
  column-gap: 3.2rem;
}

.secondSection__desc {
  display: flex;
  flex-direction: column;
  gap: 1.8rem;

  max-width: 59rem;
}

.secondSection__title {
  font-size: 3.2rem;
  line-height: 4rem;
}

.secondSection__text {
  font-family: 'GraphikLCG-light';
  font-size: 1.6rem;
  line-height: 2.8rem;
}

.secondSection__images {
  position: relative;
  max-width: 59rem;
}

.secondSection__back_img {
  position: absolute;
  z-index: -100;
}

.secondSection__img {
  width: 100%;
}

/* SECTION-3 */

.thirdSection {
  max-width: 125rem;
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  position: relative;
  z-index: 1;
}

.thirdSection__title {
  text-align: center;
  font-size: 4rem;
  line-height: 5.6rem;
}

/* SLIDER */

.slider {
  width: 100%;
  height: 37rem;
  margin: 0 auto;
  position: relative;

  overflow: hidden;
}

.slide {
  position: absolute;
  top: 0;
  left: 5%;
  width: 34rem;
  height: 31rem;
  padding: 2.2rem;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: var(--color-white);
  box-shadow: 0 0.4rem 1rem rgba(0, 0, 0, 0.1);

  transition: transform 1s;

  cursor: pointer;
}

.slider__btn {
  position: absolute;
  top: 50%;
  z-index: 10;

  border: none;
  background-color: var(--color-extralight-gray);

  height: 3.2rem;
  width: 3.2rem;
}

.slider__btn--left {
  left: -5%;
  transform: translate(-50%, -50%);
}

.slider__btn--right {
  right: -5%;
  transform: translate(50%, -50%);
  transform: rotate(180deg);
}

.slider__svg--active {
  fill: var(--color-primary);
  cursor: pointer;
}

.dots {
  position: absolute;
  bottom: 2%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.dots__dot {
  border: none;
  background-color: var(--color-mid-gray);
  height: 0.8rem;
  width: 0.8rem;
  border-radius: 50%;
  margin-right: 1.7rem;
  cursor: pointer;
  transition: all 0.5s;
}

.dots__dot:last-child {
  margin: 0;
}

.dots__dot--active {
  background-color: var(--color-primary);
}

.review {
  width: 100%;
  position: relative;
}

.review__author {
  display: grid;
  grid-template-columns: 6.5rem 1fr;
  column-gap: 2rem;
}

.review__photo {
  grid-row: 1 / span 2;
  width: 4.4rem;
  border-radius: 50%;
}

.review__name {
  font-size: 1.6rem;
  margin-bottom: 0.4rem;
}

.review__location {
  font-family: 'GraphikLCG-light';
  font-size: 1.2rem;
  line-height: 1.6px;
}

.review__text {
  font-family: 'GraphikLCG-light';
  font-size: 1.4rem;
  margin: 2rem 0;
}

/* ACCORDiON SECTION */

.accordion-section {
  margin-top: 8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.accordion__title {
  margin-bottom: 1.6rem;

  font-size: 4rem;
  line-height: 5.6rem;
}

.line {
  margin: 2.4rem 0;
  height: 0.1rem;
  width: 100%;
  background-color: var(--color-light-gray);
}

.accordion__item {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  cursor: pointer;
}

.accordion__item:hover {
  color: var(--color-primary);
  fill: var(--color-primary);
}

.accordion__item:hover .accordion__icon {
  stroke: var(--color-primary);
}

.item__header {
  display: flex;
  justify-content: space-between;
}

.item__title {
  font-size: 1.8rem;
  line-height: 3.2rem;
}

.accordion__icon {
  transform: rotate(45deg);
  stroke: var(--color-black);
  transition: ease-in-out 0.3s;
}

.item__body {
  display: block;
  font-family: 'GraphikLCG-light';
  font-size: 1.8rem;
  line-height: 3.2rem;
}

.accordion__item:not(.accordion__item_show) .item__body {
  display: none;

  max-height: 0;
  overflow: hidden;
}

.accordion__item:not(.accordion__item_show) .accordion__icon {
  transform: rotate(0deg);
}

/* SECTION INFO */

.section-info {
  margin-top: 3.8rem;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.info {
  margin-bottom: 3.8rem;
  display: flex;
  flex-direction: column;
  gap: 1.7rem;

  width: 45%;
}

.info__title {
  font-size: 1.8rem;
  line-height: 3.2rem;
}

.info__text {
  font-family: 'GraphikLCG-light';
  font-size: 1.6rem;
  line-height: 2.8rem;
}

/* FORM SECTION */

.section-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form__title {
  margin-top: 8rem;
  margin-bottom: 4.4rem;
  font-size: 4rem;
  line-height: 5.6rem;
}

.form {
  max-width: 80rem;
  column-gap: 3rem;
  gap: 3rem;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
}

.text-field {
  margin-bottom: 1rem;
}

.text-field__label {
  display: block;
  margin-bottom: 0.25rem;
}

.text-field__input {
  display: block;

  width: 38rem;

  padding: 1.6rem;

  font-family: 'GraphikLCG-light';
  font-size: 1.8rem;
  line-height: 3.2rem;

  background-color: var(--color-extralight-gray);
  border: 0.2rem solid var(--color-extralight-gray);
  border-radius: 0.3rem;

  transition: border-color 0.15s ease-in-out;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.text-field__input[type='search']::-webkit-search-decoration,
.text-field__input[type='search']::-webkit-search-cancel-button,
.text-field__input[type='search']::-webkit-search-results-button,
.text-field__input[type='search']::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.text-field__input::placeholder {
  font-family: 'GraphikLCG-light';
  font-size: 1.8rem;
  line-height: 3.2rem;
  opacity: 0.4;
}

.text-field__input:focus {
  background-color: var(--color-white);
  border-color: var(--color-mid-gray);
}

.text-field__input:disabled,
.text-field__input[readonly] {
  background-color: var(--color-white);
  opacity: 1;
}

.text-field_floating {
  position: relative;
}

.text-field_floating .text-field__input {
  padding: 1.6rem;
}

.text-field_floating .text-field__label {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  padding: 1.6rem;

  font-family: 'GraphikLCG-light';
  font-size: 1.8rem;
  line-height: 3.2rem;

  pointer-events: none;
  border: 1px solid transparent;
  transform-origin: 0 0;
  transition: opacity 0.15s ease-in-out, transform 0.15s ease-in-out;
}

.text-field_floating .text-field__input::-moz-placeholder {
  color: transparent;
}

.text-field_floating .text-field__input::placeholder {
  color: transparent;
}

.text-field_floating .text-field__input:focus,
.text-field_floating .text-field__input:not(:placeholder-shown) {
  padding-top: 1.6rem;
  padding-bottom: 1.6rem;
  background-color: var(--color-white);
  border-color: var(--color-mid-gray);
}

.text-field_floating .text-field__input:focus ~ .text-field__label,
.text-field_floating
  .text-field__input:not(:placeholder-shown)
  ~ .text-field__label {
  opacity: 0.65;
  transform: scale(0.8) translateY(-1rem) translateX(0.6rem);
}

.checkbox-container {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.checkbox {
  width: 2.4rem;
  height: 2.4rem;
}

.checkbox__label {
  font-family: 'GraphikLCG-light';
  font-size: 1.6rem;
  line-height: 2.8rem;
}

.form__btn {
  width: 38rem;

  background-color: var(--color-primary);
  color: var(--color-white);

  border: none;

  padding: 2rem 14rem;

  font-size: 2rem;
  line-height: 2.4rem;

  cursor: pointer;
  transition: ease-in-out 0.2s;
}

.form__btn:hover {
  background-color: var(--color-primary-hover);
}

/* FOOTER */

.footer {
  margin-top: 8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__span {
  height: 0.1rem;
  width: 100vw;
  background-color: var(--color-light-gray);
}

.footer__text {
  margin: 2.8rem 0;
  color: var(--color-black);
  font-family: 'GraphikLCG-light';
  font-size: 1.8rem;
  line-height: 3.2rem;
}
